import request from '@/utils/request'
export function fetchList(params) {
  return request({
    url:'/cloakDomainInfo/list',
    method:'get',
    params:params
  })
}

export function add(data) {
  return request({
    url:'/cloakDomainInfo/add',
    method:'post',
    data:data
  })
}

export function update(data) {
  return request({
    url:'/cloakDomainInfo/update',
    method:'post',
    data:data
  })
}

export function deleteDomainInfo(id) {
  return request({
    url:'/cloakDomainInfo/delete/'+id,
    method:'post',
  })
}



