<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="域名：">
            <el-input v-model="listQuery.domainName" class="input-width" placeholder="活动名称" clearable></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button size="mini" class="btn-add" @click="handleAdd()" style="margin-left: 20px">添加域名</el-button>
    </el-card>
    <div class="table-container">
      <el-table ref="flashTable"
                :data="list"
                style="width: 100%;"
                v-loading="listLoading" border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="编号" width="100" align="center">
          <template slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column label="绑定域名" align="center">
          <template slot-scope="scope">{{scope.row.domainPrefix}}</template>
        </el-table-column>
        <el-table-column label="为用户分配的域名" align="center">
          <template slot-scope="scope">{{scope.row.domainName}}</template>
        </el-table-column>
        <el-table-column label="请求域名(实际填写)" align="center">
          <template slot-scope="scope">
            {{ scope.row.domainPrefix+"/cloak?domainName=" + scope.row.domainName }}
            <el-button
              type="primary"
              icon="el-icon-search"
              circle
              size="mini"
              @click="copyToClipboard(formatDomainName (scope.row.domainName,scope.row.domainPrefix))"
              style="margin-left: 10px;"
            ></el-button>
          </template>
        </el-table-column>
        <el-table-column label="描述" align="center">
          <template slot-scope="scope">{{scope.row.description}}</template>
        </el-table-column>

        <el-table-column label="是否审核中" width="100" align="center">
          <template slot-scope="scope">{{scope.row.isCheck | formatBoolean}}</template>
        </el-table-column>

        <el-table-column label="斗篷地址(审核后的真实页面)" align="center">
          <template slot-scope="scope">{{scope.row.maskRedirectUrl}}</template>
        </el-table-column>
        <el-table-column label="审核检查地址(审核中或匹配不符用户页面)" align="center">
          <template slot-scope="scope">{{scope.row.whiteListRedirectUrl}}</template>
        </el-table-column>
        <el-table-column label="匹配规则" align="center">
          <template slot-scope="scope">
            <div v-html="formatRule(scope.row.matchRule)"></div>
          </template>
        </el-table-column>


        <el-table-column label="创建时间" width="180" align="center">
          <template slot-scope="scope">{{scope.row.createTime|formatDate}}</template>
        </el-table-column>

        <el-table-column label="操作" width="180" align="center">
          <template slot-scope="scope">
            <el-button size="mini"
                       type="text"
                       @click="handleUpdate(scope.$index, scope.row)">
              编辑
            </el-button>
            <el-button size="mini"
                       type="text"
                       @click="handleDelete(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      :before-close="handleClose()"
      width="30%">
      <el-form ref="defaultDomainInfoForm":model="defaultDomainInfo"  label-width="120px">
        <el-form-item label="域名" prop="name">
          <el-input v-model="defaultDomainInfo.domainName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="绑定域名" prop="name">
          <el-input v-model="defaultDomainInfo.domainPrefix" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="name">
          <el-input v-model="defaultDomainInfo.description" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="是否开启审核" prop="name">
          <el-input v-model="defaultDomainInfo.isCheck" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="斗篷地址(审核后的真实页面)" prop="name">
        <el-input v-model="defaultDomainInfo.maskRedirectUrl" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="审核检查地址" prop="name">
          <el-input v-model="defaultDomainInfo.whiteListRedirectUrl" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="匹配规则" prop="matchRule">
          <div v-for="(rule, index) in defaultDomainInfo.matchRule" :key="index" class="rule-item">
            <el-select v-model="rule.matchRuleType" placeholder="选择规则类型">
              <el-option label="所属国家" value="REGION"></el-option>
              <el-option label="语言" value="LANGUAGE"></el-option>
              <el-option label="是否移动设备" value="IS_MOBILE"></el-option>
              <el-option label="是否开启代理" value="IS_PROXY"></el-option>
            </el-select>
            <el-switch v-model="rule.condition" active-text="是" inactive-text="否"></el-switch>
            <el-input v-model="rule.value" placeholder="输入值"></el-input>
            <el-button type="danger" icon="el-icon-delete" @click="removeRule(index)"></el-button>
          </div>
          <el-button type="primary" icon="el-icon-plus" @click="addRule">添加规则</el-button>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleConfirm('productAttrCatForm')">确 定</el-button>
      </span>
    </el-dialog>


  </div>
</template>
<script>
  import {fetchList, update, deleteDomainInfo, add} from '@/api/cloak';
  import {formatDate} from '@/utils/date';

  const defaultListQuery = {
    pageNum: 1,
    pageSize: 5,
    domainName: null
  };
  const defaultDomainInfo = {
    id: null,
    domainName: null,
    description: null,
    isCheck: 1,
    maskRedirectUrl: null,
    whiteListRedirectUrl: null,
    matchRule: []
  };
  export default {
    name: 'cloakDomainInfoList',
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        list: null,
        total: null,
        listLoading: false,
        dialogVisible: false,
        dialogTitle:'',
        flashPromotion: Object.assign({}, defaultDomainInfo),
        defaultDomainInfo : {
          id: null,
          domainName: null,
          domainPrefix: null,
          description: null,
          isCheck: false,
          maskRedirectUrl: null,
          whiteListRedirectUrl: null,
          matchRule: []
        },
        tableData: [
          {
            matchRule: {
              matchRuleType: 'REGION',
              condition: true,
              value: 'China'
            }
          },
          {
            matchRule: {
              matchRuleType: 'LANGUAGE',
              condition: false,
              value: 'English'
            }
          },
          {
            matchRule: {
              matchRuleType: 'IS_MOBILE',
              condition: true,
              value: ''
            }
          },
          {
            matchRule: {
              matchRuleType: 'IS_PROXY',
              condition: false,
              value: ''
            }
          }
        ]
      }
    },
    created() {
      this.getList();
    },
    filters: {
      formatActiveStatus(row) {
        let nowDate = new Date();
        let startDate = new Date(row.startDate);
        let endDate = new Date(row.endDate);
        if (nowDate.getTime() >= startDate.getTime() && nowDate.getTime() <= endDate.getTime()) {
          return '活动进行中';
        } else if (nowDate.getTime() > endDate.getTime()) {
          return '活动已结束';
        } else {
          return '活动未开始';
        }
      },
      formatDomainName(domainName){
        return "https://hhuut778.xyz/cloak?domainName=" + domainName;
      },
      formatDate(time){
        if(time==null||time===''){
          return 'N/A';
        }
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
      },
      formatBoolean(boolean){
        if(boolean){
          return '是';
        }else{
          return '否';
        }
      }
    },
    methods: {
      formatDomainName(domainName,domainPrefix){
        return domainPrefix +"/cloak?domainName=" + domainName;
      },
      copyToClipboard(text) {
        const input = document.createElement('input');
        input.setAttribute('value', text);
        document.body.appendChild(input);
        input.select();
        document.execCommand('copy');
        document.body.removeChild(input);
        this.$message({
          message: '复制成功',
          type: 'success'
        });
      },
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getList();
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getList();
      },

      handleAdd() {
        this.dialogVisible = true;
        this.dialogTitle = "添加域名";
      },
      handleUpdate(index, row) {
        this.dialogVisible = true;
        this.dialogTitle = "编辑域名";
        this.defaultDomainInfo.domainName = row.domainName;
        this.defaultDomainInfo.id = row.id;
        this.defaultDomainInfo.description = row.description;
        this.defaultDomainInfo.isCheck = row.isCheck;
        this.defaultDomainInfo.maskRedirectUrl = row.maskRedirectUrl;
        this.defaultDomainInfo.whiteListRedirectUrl = row.whiteListRedirectUrl;
        this.defaultDomainInfo.matchRule = row.matchRule;
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getList();
      },
      handleDelete(index, row) {
        this.$confirm('是否要删除该活动?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteFlash(row.id).then(response => {
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.getList();
          });
        });
      }
    , formatRule(matchRules){
      console.log('matchRules:', matchRules); // 添加日志输出
      if (!Array.isArray(matchRules)) return '';

      const matchRuleTypeDesc = {
        REGION: '所属国家',
        LANGUAGE: '语言',
        IS_MOBILE: '是否移动设备',
        IS_PROXY: '是否开启代理'
      }

      return matchRules.map(matchRule => {
        const ruleType = matchRuleTypeDesc[matchRule.matchRuleType] || matchRule.matchRuleType;
        const condition = matchRule.condition ? '是' : '否';
        const value = matchRule.value || '';
        return `${ruleType}  ${condition}   ${value}`;
      }).join('<br> ');
    },
      handleConfirm(formName){
            let data = this.defaultDomainInfo;

            if(this.dialogTitle==="添加域名"){
              add(data).then(response=>{
                this.$refs[formName].resetFields();
                this.$message({
                  message: '添加成功',
                  type: 'success',
                  duration:1000
                });
                this.dialogVisible = false;
                this.getList();
              });
            }else{
              update(data).then(response=>{
                this.$message({
                  message: '修改成功',
                  type: 'success',
                  duration:1000
                });
                this.dialogVisible = false;
                this.getList();
              });
            }
      },

      handleClose(){
        if (!this.dialogVisible && this.$refs.defaultDomainInfoForm) {
          this.$refs.defaultDomainInfoForm.clearValidate()
        }
      },
      getList() {
        this.listLoading = true;
        fetchList(this.listQuery).then(response => {
          this.listLoading = false;
          this.list = response.data.records;
          this.total = response.data.total;
        });
      },
      addRule() {
        this.defaultDomainInfo.matchRule.push({
          matchRuleType: '',
          condition: false,
          value: ''
        });
      },
      removeRule(index) {
        this.defaultDomainInfo.matchRule.splice(index, 1);
      }
    }
  }
</script>
<style></style>





